const LOCAL_STORAGE_STORE_NAME = 'state';

export const loadState = () => {
  try {
    const serializedState = localStorage.getItem(LOCAL_STORAGE_STORE_NAME);
    if (serializedState === null) {
      return undefined;
    }
    return JSON.parse(serializedState);
  } catch (err) {
    return undefined;
  }
};

export const saveState = (state) => {
  try {
    const serializedState = JSON.stringify(state);
    localStorage.setItem(LOCAL_STORAGE_STORE_NAME, serializedState);
  } catch (err) {
    // Ignore write errors.
  }
};
